code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}
input{
  font-size: 1.3rem;
  width: 100%;
  color: grey;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p{
  padding:  5px;
text-align: center;
}

.card img{
  max-width: 150px;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}